import React from 'react';
import { connect } from 'react-redux'
import { DRINKS_LIST_VIEW_FIND_BY_NR, DRINKS_LIST_VIEW_LIST } from '../drinks-reducer'
import { setDrinksListView } from '../drinks-actions'
import Btn from '../../btn/Btn_'

const stateToProps = state => {
    const { drinks } = state.drinks
    const { view } = state.drinks.drinksList
    return {
        drinks,
        view
    };
};

function dispatchToProps(dispatch) {

    return {
        setDrinksListView: (view => {
            dispatch(setDrinksListView(view))
        }),
    };
}

function DrinksFindByIdxBtn({ drinks = [], view = '', setDrinksListView }) {

    const nrBtn = <Btn
        text='Podaj nr drinka'
        cssClass='drinks-find-by-nr'
        clickFn={() => setDrinksListView(DRINKS_LIST_VIEW_FIND_BY_NR)}
        disabled={!drinks.length} />

    const listBtn = <Btn
        text='PeÅna lista'
        cssClass='drinks-find-by-nr'
        clickFn={() => setDrinksListView(DRINKS_LIST_VIEW_LIST)} />

    return (DRINKS_LIST_VIEW_LIST === view
        ? nrBtn
        : (DRINKS_LIST_VIEW_FIND_BY_NR === view
            ? listBtn
            : '')
    )
}

export default connect(stateToProps, dispatchToProps)(DrinksFindByIdxBtn)
