import React from 'react';
import { connect } from 'react-redux'
import Kbrd from './Kbrd_'
import { filterDrinks } from '../drinks/drinks-actions'

const stateToProps = state => {
    const { drinksList } = state.drinks
    return {
        nameOrIng: drinksList.nameOrIng
    };
};

function dispatchToProps(dispatch) {
    return {
        filterDrinks: nameOrIng => dispatch(filterDrinks(nameOrIng))
    };
}

const keyRows = [
    ['Ä', 'Ä', 'Ä', 'Å', 'Å', 'Ã³', 'Å', 'Åº', 'Å¼'],
    ['q', 'w', 'e', 'r', 't', 'y', 'u', 'i', 'o', 'p'],
    ['a', 's', 'd', 'f', 'g', 'h', 'j', 'k', 'l'],
    ['z', 'x', 'c', 'v', 'b', 'n', 'm']
]

const keyBtm = ' '

function Keyboard({ nameOrIng = '', filterDrinks }) {

    const canFn = nameOrIng.length > 2
    const cssBtmActive = nameOrIng.length > 2 ? ' kbrd-active' : ' kbrd-inactive'

    return <Kbrd {...{
        // cssTopActive,
        placeholder: '',
        txt: nameOrIng,
        clearFn: () => filterDrinks(''),
        bkspaceFn: () => filterDrinks(nameOrIng.slice(0, -1)),
        keyClickFn: key => filterDrinks(nameOrIng + key),
        keyRows,
        keyBtm,
        cssBtmActive,
        showFn: () => canFn ? console.log('todo') : false
    }} />
}

export default connect(stateToProps, dispatchToProps)(Keyboard)
