import React from 'react';
import { connect } from 'react-redux';
import IngPrefs from './IngPrefs_'
import { ingPrefsToggleWant, ingPrefsToggleNotWant } from '../ings-actions'
import './ing-prefs.scss'

function dispatchToProps(dispatch) {
    return {
        want: ing => dispatch(ingPrefsToggleWant(ing)),
        notWant: ing => dispatch(ingPrefsToggleNotWant(ing))
    };
}

function IngsPrefsWantNot({ ing, want, notWant }) {

    const cssClass = ing.usable && ing.notWanted ? ' ing-prefs-wanted-not' : ''

    return <IngPrefs
        {...{
            ing,
            clickFn: notWant,
            cssClass
        }} />
}

export default connect(null, dispatchToProps)(IngsPrefsWantNot);
