import React from 'react';
import KbrdRowTop from './KbrdRowTop_'
import KbrdRowKeys from './KbrdRowKeys_'
import KbrdRowBtm from './KbrdRowBtm_'
import './kbrd.scss'

function Kbrd_({ cssClass = '',
    placeholder = '', txt = '', clearFn, bkspaceFn, cssTopActive = '',
    keyRows = [], keyClickFn,
    keyBtm = '', showFn, cssBtmActive = '' }) {

    const text = txt.length ? txt : placeholder
    const cssActive = txt.length ? ' kbrd-active' : ' kbrd-inactive'

    return (<div className={'kbrd ' + cssClass}>
        <KbrdRowTop
            {...{
                text,
                cssTopActive: cssTopActive || cssActive,
                clearFn,
                bkspaceFn
            }} />

        {keyRows.map((keys, idx) => {
            return <KbrdRowKeys
                key={'kbrd-row-' + idx}
                {...{ keys, keyClickFn }} />
        })}

        <KbrdRowBtm
            {...{
                keyBtm,
                cssBtmActive: cssBtmActive || cssActive,
                keyClickFn,
                showFn
            }} />
    </div>)
}

export default Kbrd_