import React from 'react';
import Drink from '../drink/Drink_'
import { connect, batch } from 'react-redux';
import { setDrinksRandView, setDrinksRandDrink } from '../drinks-actions'
import { DRINKS_RAND_VIEW, DRINKS_RAND_VIEW_DRAWING } from '../drinks-reducer'
import Btn from '../../btn/Btn_'
import ReactVivus from 'react-vivus';
import './drink-random.scss'
import svg from './complexity-nano.svg';

const stateToProps = state => {
    const { drink } = state.drinks
    const { view } = state.drinks.drinksRand

    return {
        drink,
        view
    };
};

function dispatchToProps(dispatch) {
    return {
        drawDrink: () => {
            batch(() => {
                dispatch(setDrinksRandDrink())
                dispatch(setDrinksRandView(DRINKS_RAND_VIEW_DRAWING))
            })
        },
        showDrink: () => dispatch(setDrinksRandView(DRINKS_RAND_VIEW))
    }
}

function DrinksRandom({ view = '', drink = {}, drawDrink, showDrink }) {

    const drinkView = view === DRINKS_RAND_VIEW
        ? <Drink drink={drink} />
        : <ReactVivus
            id='drinks-random-drawing'
            option={{
                file: svg,
                duration: 70,
                // animTimingFunction: 'linear',
                // animTimingFunction: 'ease',
                // animTimingFunction: 'ease-in',
                animTimingFunction: 'ease-out',
                // animTimingFunction: 'ease-out bounce',
                // pathTimingFunction: 'linear',
                // pathTimingFunction: 'ease',
                // pathTimingFunction: 'ease-in',
                // pathTimingFunction: 'ease-out',
                pathTimingFunction: 'ease-out bounce',
                // type: 'delayed',
                // type: 'sync',
                type: 'oneByOne'//,
                // onReady: () => setRandomDrink()
            }}
            // style={{ height: '100%', width: 'auto' }}
            callback={() => showDrink()}
        />

    // <img className='cats-home-btn '
    //     src='complexity-nano.svg'
    //     alt='complexity-nano.svg'>
    // </img>


    const btnDisabled = view !== DRINKS_RAND_VIEW

    return (<>

        {drinkView}

        <div className='drinks-random'>

            <Btn
                text='Losuj drinka'
                cssClass='drinks-random'
                clickFn={() => drawDrink()}
                disabled={btnDisabled}>

                <img src='categories/cat-random.svg' alt={''} className='drinks-random'></img>

                <img src='categories/cat-random.svg' alt={''} className='drinks-random'></img>
            </Btn>

        </div>
    </>)
}

export default connect(stateToProps, dispatchToProps)(DrinksRandom);
