import React from 'react';
import { connect } from 'react-redux'
import './drink-glass.scss'

const stateToProps = state => {
    const { max, total, ingsOrder } = { ...state.drinks.drinkCustom }
    return {
        ings: state.drinks.ings,
        max,
        total,
        ingsOrder
    };
};

function DrinksGlass({ ings, max, total, ingsOrder }) {

    const ratio = total / max
    const heigh = 166; // 204 - 38
    const width = 24; // 76 - 52
    const hPush = ratio * heigh;
    const wPush = ratio * width;
    const ptrx = 164 + wPush;
    const ptry = 204 - hPush;
    const ptlx = 76 - wPush;
    const ptly = 204 - hPush;

    return (<div className='drink-custom-glass'>

        <svg className='drink-custom-glass drink-custom-glass'
            viewBox='0 0 240 240'>

            <g className='drink-custom-glass-fill'>
                <polygon points={'76,204 164,204 ' + ptrx + ',' + ptry + ' ' + ptlx + ',' + ptly}
                    style={{ 'stroke': 'purple', 'strokeWidth': 1 }} />
            </g>

            <use xlinkHref='#drink-custom-glass' />

        </svg>

    </div>)
}

export default connect(stateToProps)(DrinksGlass)