import React from 'react';
import { connect } from 'react-redux';
import IngPrefs from './IngPrefs_'
import { ingPrefsToggleWant, ingPrefsToggleNotWant } from '../ings-actions'
import './ing-prefs.scss'

function dispatchToProps(dispatch) {
    return {
        want: ing => dispatch(ingPrefsToggleWant(ing)),
        notWant: (event, ing) => {
            dispatch(ingPrefsToggleNotWant(ing))
            event.stopPropagation()
        }
    };
}

function IngsPrefsWant({ ing, want, notWant }) {

    const cssClass = ing.usable && ing.wanted ? ' ing-prefs-wanted' : ''

    return <IngPrefs
        {...{
            ing,
            clickFn: want,
            cssClass
        }} />
}

export default connect(null, dispatchToProps)(IngsPrefsWant);
