import React from 'react';

function KbrdRowKeys_({ keys = [], keyClickFn }) {

    return (<div className='kbrd-row kbrd-row-keys'>
        {keys.map(key => {
            return (<span
                onClick={() => keyClickFn(key)}
                key={'kbrd-key-' + key}
                className='kbrd-btn'>
                {key}
            </span>)
        })}
    </div>)
}

export default KbrdRowKeys_