import React from 'react';
import './category-icon.scss'

const viewBoxes = {
    'cat-alco': '0 0 72 32',
    'cat-list': '0 0 55 45',
    'cat-custom': '0 0 28 42',
    'cat-prefs': '0 0 160 160',
    'cat-random': '0 0 160 160',
    'cat-top': '0 0 83 160'
}

function CategoryIcon_({ category }) {

    const cat = 'cat-' + category.id;

    return (<div className={'category-icon ' + cat}>

        <span className={'category-icon ' + cat}>

            <img className={'category-icon ' + cat}
                alt=''
                src='data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'>
            </img>

            <svg className={'category-icon ' + cat}
                viewBox={viewBoxes[cat]}>
                {/* viewBox='0 0 60 60'> */}
                <use xlinkHref={'#' + cat} />
            </svg>
        </span>
    </div >)
}

export default CategoryIcon_
