import React from 'react';
import { sortBy, toArray } from 'lodash'
import { connect } from 'react-redux';
import IngsCustom from './ings-custom/IngCustom'
import IngsPrefsWant from './ings-prefs/IngPrefsWant'
import IngsPrefsWantNot from './ings-prefs/IngPrefsWantNot'
import './ings.scss'

const stateToProps = state => {
    const cat = state.categories.category
    const ingsViewName = 'Ings' + cat.id[0].toUpperCase() + cat.id.slice(1)

    return {
        ings: sortBy(toArray(state.drinks.ings), 'name'),
        ingsViewName
    };
};

function Ings({ ings = [], ingsViewName = '', ingsKind = '', cssClass = '' }) {

    const ingsViews = {
        IngsCustom,
        IngsPrefsWant,
        IngsPrefsWantNot
    }

    const IngsView = ingsViews[ingsViewName + ingsKind]

    return (<ul className={'ings ' + cssClass}>
        {ings.map((ing, idx, arr) => {
            return (<li key={ing.id} className={'ings ' + cssClass}>
                <IngsView key={ing.id} ing={ing} />
            </li>)
        })}
    </ul>)
}

export default connect(stateToProps)(Ings);
