import { createReducer } from '@reduxjs/toolkit'
import { countBy, forEach, shuffle } from 'lodash'

import {
    setDrinksListView, setDrink,
    setDrinksRandView, setDrinksRandDrink, setTopDrinks, setDrinksData,
    resetDrinks, setDrinksPrefsView, setCustomDrink,
    setDrinkNr, filterDrinks
} from './drinks-actions'

import {
    ingPrefsToggleWant, ingPrefsToggleNotWant, ingPrefsReset,
    ingCustomAdd, ingCustomReset
} from '../ings/ings-actions'

import { selectDrinks, randDrink, filterDrink, makeNameAndIngsStr } from './drinks'
import json from '../tools/json'
const { fromtojson } = json

export const DRINKS_LIST_VIEW_LIST = 'drinks_list_view_list'
export const DRINKS_LIST_VIEW_FILTER = 'drinks_list_view_filter'
export const DRINKS_LIST_VIEW_FIND_BY_NR = 'drinks_list_view_find_by_nr'
export const DRINKS_RAND_VIEW = 'drinks_rand_view'
export const DRINKS_RAND_VIEW_DRAWING = 'drinks_rand_view_drawing'
export const DRINKS_PREFS_VIEW_INGS = 'drinks_prefs_view_ings'
export const DRINKS_PREFS_VIEW_DRINKS_LIST = 'drinks_prefs_view_drinks_list'

const drinkGlassMax = 200 // TODO config of glass capacity
const topNr = 10

const initialState = {
    // sourceDrinks: [],
    // sourceIngs: {},

    drinks: [],
    drink: {},
    ings: {},

    drinksList: {
        view: DRINKS_LIST_VIEW_LIST,
        nameOrIng: '',
        nr: ''
    },

    drinksPrefs: {
        view: DRINKS_PREFS_VIEW_INGS
    },

    drinksRand: {
        view: DRINKS_RAND_VIEW
    },

    drinkCustom: {
        ings: [],
        max: drinkGlassMax,
        total: 0,
        ingsOrder: [],
        ingsCounts: {},
        left: drinkGlassMax
    }
}

const resetToInitialState = state => {
    forEach(initialState, (v, k) => state[k] = v);
}

// https://redux-toolkit.js.org/api/createreducer/
const drinkReducer = createReducer(initialState, {

    [setDrinksData]: (state, action) => {
        state.sourceDrinks = (action.payload.drinks || [])
            .map(makeNameAndIngsStr)

        state.sourceIngs = action.payload.ings || []
    },

    [resetDrinks]: (state, acion) => {
        resetToInitialState(state)
    },

    [setDrink]: (state, action) => {
        state.drink = action.payload
    },

    [setDrinksListView]: (state, action) => {
        state.drinksList.view = action.payload || DRINKS_LIST_VIEW_LIST
        state.drinks = fromtojson(state.sourceDrinks)

        if (state.drinksList.view === DRINKS_LIST_VIEW_LIST) {
            state.drinksList = { ...initialState.drinksList }
        }

        if (state.drinksList.view === DRINKS_LIST_VIEW_FILTER) {
            state.drinks = []
        }

        if (state.drinksList.view === DRINKS_LIST_VIEW_FIND_BY_NR) {
            state.drinks = []
        }
    },

    [setDrinkNr]: (state, action) => {
        const nr = action.payload
        state.drinks = []

        if (nr.length < 3) {
            state.drinksList.nr = nr

            if (nr.length > 0) {
                state.drinks = state.sourceDrinks
                    .filter(d => d.nr === state.drinksList.nr)
            }
        }
    },

    [filterDrinks]: (state, action) => {
        const nameOrIng = action.payload
        state.drinksList.nameOrIng = nameOrIng
        state.drinks = []

        if (nameOrIng.length >= 3) {
            state.drinks = state.sourceDrinks
                .filter(d => filterDrink(d, nameOrIng))
        }
    },

    [setDrinksPrefsView]: (state, action) => {
        state.drinksPrefs.view = action.payload || DRINKS_PREFS_VIEW_INGS
        state.ings = !action.payload
            ? fromtojson(state.sourceIngs)
            : state.ings
    },

    [ingPrefsToggleWant]: (state, action) => {
        const ingId = action.payload.id
        const ing = state.ings[ingId]
        const wanted = ing.wanted

        state.ings[ingId].wanted = !wanted
        state.ings[ingId].notWanted = false

        const drinks = selectDrinks(
            state.sourceDrinks,
            state.ings
        )

        state.drinks = drinks.length === state.sourceDrinks.length
            ? []
            : drinks
    },

    [ingPrefsToggleNotWant]: (state, action) => {
        const ingId = action.payload.id
        const ing = state.ings[ingId]
        const notWanted = ing.notWanted

        state.ings[ingId].wanted = false
        state.ings[ingId].notWanted = !notWanted

        const drinks = selectDrinks(
            state.sourceDrinks,
            state.ings
        )

        state.drinks = drinks.length === state.sourceDrinks.length
            ? []
            : drinks
    },

    [ingPrefsReset]: (state, action) => {
        state.ings = fromtojson(state.sourceIngs)

        state.drinks = []
    },

    [setTopDrinks]: (state, action) => {
        state.drinks = shuffle(fromtojson(state.sourceDrinks))
            .slice(0, topNr)
    },

    [setDrinksRandView]: (state, action) => {
        state.drinksRand.view = action.payload || DRINKS_RAND_VIEW
        state.drinks = state.drinks.length ? state.drinks : fromtojson(state.sourceDrinks)
    },

    [setDrinksRandDrink]: (state, action) => {
        state.drink = randDrink(state.drinks)
    },

    [setCustomDrink]: (state, action) => {
        state.ings = fromtojson(state.sourceIngs)
    },

    [ingCustomAdd]: (state, action) => {
        const ing = action.payload
        const { ings, max, total, ingsOrder } = { ...state.drinkCustom }
        const newTotal = total + ing.portion
        if (newTotal <= max) {
            state.drinkCustom.ings.push(ing)
            state.drinkCustom.total = newTotal
            state.drinkCustom.ingsCounts = countBy(ings, ing => ing.id)
            state.drinkCustom.left = max - newTotal

            if (ingsOrder[ingsOrder.length - 1] !== ing.id) {
                state.drinkCustom.ingsOrder.push(ing.id)
            }
        }
    },

    [ingCustomReset]: (state, action) => {
        state.ings = fromtojson(state.sourceIngs)
        state.drinkCustom = { ...initialState.drinkCustom }
    }

})

export default drinkReducer
