import React from 'react';
import { connect } from 'react-redux'
import Kbrd from './Kbrd_'
import { setDrinkNr } from '../drinks/drinks-actions'

const stateToProps = state => {
    const { nr } = state.drinks.drinksList
    const drinksLen = state.drinks.drinks.length

    return {
        nr,
        drinksLen
    };
};

function dispatchToProps(dispatch) {
    return {
        setDrinkNr: nr => dispatch(setDrinkNr(nr)),
    };
}

const keyRows = [
    [1, 2, 3],
    [4, 5, 6],
    [7, 8, 9]
]

const keyBtm = 0

function DigiPad({ nr, drinksLen, setDrinkNr, findDrinkByNr }) {

    const cssBtmActive = (nr.length && drinksLen > 1) ? ' kbrd-active' : ' kbrd-inactive'

    return <Kbrd {...{
        cssClass: 'kbrd-digipad',
        placeholder: '',
        txt: nr,
        clearFn: () => setDrinkNr(''),
        bkspaceFn: () => setDrinkNr(nr.slice(0, -1)),
        keyClickFn: key => setDrinkNr(nr + key),
        keyRows,
        keyBtm,
        showFn: () => (drinksLen > 1) ? findDrinkByNr(nr) : false,
        cssBtmActive
    }} />
}

export default connect(stateToProps, dispatchToProps)(DigiPad)
